*,::after,::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background: hsl(210, 36%, 96%);
  color: hsl(209,61%,16%);
  line-height: 1.5;
  font-size: .875rem;
}
.block{
  min-height: 50vh;
}
.bcg-black{
  background: #2c2e31;
}

.container{
  width: 90vw;
  max-width: 730px;
  margin: 0 auto;
  margin-top: -200px;
  border-radius: .25rem;
  position: relative;
  text-align: center;
  padding: 1.5rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .2);
}

.container::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 130px;
  border-bottom-left-radius: .25rem;
  border-bottom-right-radius: .25rem;
  border-bottom: 1px solid rgba(0, 0, 0, .25);
}

.container img{
  width: 150px;
  height: 150px;
  position: relative;
  border-radius: 50%;
  padding: 5px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .25);
  margin-bottom: 2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .2);
}

.btn{
  text-transform: uppercase;
  background-color: hsl(205, 78%, 60%);
  padding: .375rem;
  letter-spacing: .1rem;
  display: block;
  transition: all .3s linear;
  font-size: .875rem;
  border: 2px solid hsl(205, 78%, 60%);
  cursor: pointer;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
  border-radius: .25rem;
  margin: 0 auto;
  margin-top: 1.5rem;
}

.btn:hover{
  color: hsl(205, 78%, 60%);
  background-color: hsl(205, 86%, 17%);
  border-color: hsl(205, 86%, 17%);
}

.user-title{
  font-size: 18px;
  margin-bottom: 0;
}

.user-value{
  font-size: 28px;
}

.values-list{
  display: grid;
  grid-template-columns: repeat(6,1fr);
}

.icon{
  background: transparent;
  border: transparent;
  font-size: 1.5rem;
  cursor: pointer;
  color: hsl(210, 22%, 49%);
  transition: all .3s linear;
}

p{
  margin-bottom: 1.5rem;
  color: hsl(209, 34%, 30%);
}

.icon:hover{
  color: hsl(205, 78%, 60%)
}


@media screen and (min-width: 800px) {
  .body{
    font-size: 1rem;
  }

  .user-value{
    font-size: 38px;
  }

  .icon{
    font-size: 2 rem;
  }
  
}